.sliderContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2.5rem 0 2rem 0;
}
.slider {
  position: relative;
  width: 100%;
  max-width: 900px;
  height: 340px;
  border-radius: 2rem;
  overflow: hidden;
  box-shadow: 0 4px 24px rgba(166,140,182,0.10);
  background: #fff8f5;
  display: flex;
  align-items: center;
}
.slide {
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  opacity: 0;
  transition: opacity 0.7s cubic-bezier(.4,0,.2,1);
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 3rem;
  z-index: 1;
}
.slide.active {
  opacity: 1;
  z-index: 2;
}
.slideContent {
  background: rgba(255, 255, 255, 0.85);
  border-radius: 1.2rem;
  padding: 2rem 2.5rem;
  box-shadow: 0 2px 12px rgba(166,140,182,0.08);
  max-width: 420px;
}
.slideContent h2 {
  font-size: 2rem;
  color: #000;
  font-family: 'Poppins', 'Open Sans', Arial, sans-serif;
  margin-bottom: 0.7rem;
  font-weight: 600;
}
.slideContent p {
  font-size: 1.1rem;
  color: #3B2C35;
  margin: 0;
}
.sliderDots {
  position: absolute;
  left: 50%;
  bottom: 1.2rem;
  transform: translateX(-50%);
  display: flex;
  grid-gap: 0.7rem;
  gap: 0.7rem;
  z-index: 3;
}
.dot {
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #f6c177;
  opacity: 0.4;
  cursor: pointer;
  transition: opacity 0.2s, background 0.2s;
  border: 2px solid #fff8f5;
}
.dot.active {
  opacity: 1;
  background: #000;
}
@media (max-width: 700px) {
  .slider {
    height: 220px;
    border-radius: 1.2rem;
  }
  .slideContent {
    padding: 1.1rem 1.2rem;
    border-radius: 0.7rem;
    max-width: 260px;
  }
  .slideContent h2 {
    font-size: 1.1rem;
  }
  .slideContent p {
    font-size: 0.9rem;
  }
  .slide {
    padding-left: 1rem;
  }
}
.aboutSection {
